// Menu

.menu {
  display: none;
  padding: 10px;
  position: absolute;
  top: 99%;
  left: 0;
  right: 0;
  font-size: 0;
  background: $greyLight;
  box-shadow: 0 8px 15px rgba(0,0,0,.1);

  &__btn {
    position: absolute;
    top: 26px;
    right: 20px;
    transform: rotate(0);
    transition: .5s ease-in-out;
    cursor: pointer;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    z-index: 1;
    display: block;
    width: 30px;
    height: 20px;
    padding: 4px 0;

    &-line {
      content: "";
      position: absolute;
      left: 0;
      display: block;
      width: 100%;
      height: 2px;
      background: $white;
      border-radius: 2px;
      transform: rotate(0);
      transition: .25s ease-in-out;

      &_top {
        top: 0;
      }

      &_mid,
      &_mid2 {
        top: calc(50% - 1px);
      }

      &_btm {
        bottom: 0;
      }
    }

    &.is-active &-line {

      &_top,
      &_btm {
        top: 7px;
        left: 50%;
        width: 0;
        opacity: 0;
      }

      &_mid {
        transform: rotate(45deg);
      }

      &_mid2 {
        transform: rotate(-45deg);
      }
    }
  }

  &__item {
    display: block;
    line-height: 32px;
    font-size: 16px;
    border-bottom: 1px solid $white;
    transition: color .35s;

    &:last-child {
      border: none;
    }

    &:hover {
      color: $linkHover;
    }
  }

  @media screen and (min-width: 768px) {
    z-index: 5;

    &__btn {
      top: 0;
      bottom: 0;
      margin: auto;
    }

    &__item {
      display: inline-block;
      width: 25%;
      border: none;
    }
  }

  @media screen and (min-width: 1024px) {
    position: static;
    padding: 0;
    display: flex;
    align-items: center;
    background: none;
    box-shadow: none;

    &__btn {
      display: none;
    }

    &__item {
      width: auto;
      margin-right: 20px;
      color: $white;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 0;
        background: $white;
        transition: width .5s ease-out;
      }

      &:hover {
        color: $white;

        &:after {
          width: 100%;
        }
      }

      &:last-child {
        margin: 0;
      }
    }
  }
}

