// Slick slider

.slick-dots {

  li {
    width: 10px;
    height: 10px;
    border: 1px solid #cecece;
    border-radius: 50%;

    button {
      width: 8px;
      height: 8px;

      &:before {
        width: 100%;
        height: 100%;
        color: transparent;
        border-radius: 50%;
      }
    }

    &.slick-active {
      border-color: #afafaf;

      button:before {
        opacity: 1;
        background: #cecece;
        color: transparent;
      }
    }
  }
}