// Card

.card {
  outline: none;
  text-align: center;


  &__title {
    margin: 10px 0 12px;
    display: block;
    font-size: 24px;
    font-family: $circleBold;
  }


  &__img {
    width: 100%;

    &._acc {
      height: 225px;
    }
  }


  &__price {

  }


  @media all and(min-width: 768px) {
    margin: 0 20px 20px;
  }
}

.card-list {
  margin: 0 -20px;
}