// Social

.social {
  margin: auto;
  max-width: 300px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__item {
    flex: 0 1 auto;
    font-size: 34px;
    transition: transform .25s ease-in-out;

    &:hover {
      //color: $linkHover;
      transform: scale(1.05);
    }
  }
}