// About

.about {

  &__photo {
    width: calc(100% + 30px);
    margin: 0 -15px 30px;
  }

  &__text {

    p + p {
      margin-top: 10px;
    }
  }

  @media screen and (min-width: 768px) {
    display: flex;

    &__photo {
      width: 200px;
      margin: 0 20px 20px 0;
      float: left;
    }
  }
}